import React from 'react';
import './Home.css';

const boxesLength = 45;
const name = 'Kamen';
const surname = 'Kamenov';
const proff = 'front';
const subProff = 'end';
const role = 'developer';
const words = [name, surname, proff, subProff, role];
const rowLength = 9;

let boxes = new Array(boxesLength);

boxes.fill({});
boxes = boxes.map(() => {
    return {
        className: 'box',
        content: '',
        accent: '',
    }
});

let i = 0;
let k = 0;
let cell = 0;

for (i = 0; i < words.length; i++) {
    cell = i * rowLength;
    for(k = 0; k < words[i].length; k++) {
        boxes[cell + k].content = words[i][k];         
        boxes[cell + k].accent = 'box--accent';         
    }
}

class Home extends React.Component {
    render() {
        return (
            <React.Fragment>
                {/* <section className='section home'>
                    <h1 className='section__title home__title'>KAMEN KAMENOV</h1>
                    <h2 className="home__subtitle">front-end developer</h2>
                </section> */}

                <section className="section box-section">
                    {boxes.map((e, i) => <div key={i} className={`${e.className} ${e.className}-${i} ${e.accent}`}>{e.content}</div>)}
                </section>
            </React.Fragment>
        )
    }
}

export default Home;


